<template>
  <div class="goods">
    <div>
      <ul class="cl">
        <li class="menu_select">
          <router-link to="/recommend" class="exact">热门推荐</router-link>
        </li>
        <li>
          <router-link to="/storage">存号箱</router-link>
        </li>
      </ul>
    </div>
    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
      <van-cell v-for="item in list" :key="item.dt" :title="item.tab_name" />
    </van-list>
  </div>
</template>

<script>
import { List, Cell } from "vant";
import axios from "axios";
export default {
  components: {
    [List.name]: List,
    [Cell.name]: Cell
  },
  data() {
    return {
      list: [],
      loading: false,
      finished: false
    };
  },

  methods: {
    onLoad() {
      // 异步更新数据
      this.loading = true;
      axios
        .post(process.env.VUE_APP_API + "login/sure", {
          params: this.list.length
        })
        .then(response => {
          console.log("response", this.list);
          if (this.list.length == 0) {
            this.list = response.data.data;
          } else {
            response.data.data.forEach((item, index, array) => {
              this.list.push(item);
            });
          }
          // 加载状态结束
          this.loading = false;
          // 数据全部加载完成
          if (this.list.length >= 40) {
            this.finished = true;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
      // setTimeout(() => {
      //   for (let i = 0; i < 10; i++) {
      //     this.list.push(this.list.length + 1);
      //   }

      // }, 500);
    }
  }
};
</script>

<style lang="less">
.cl {
  li {
    float: left;
  }
}
</style>
